/*Color variables*/
/*Width variables*/
/*Base reset of styles*/
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

img, embed, object, video {
  width: 100%;
  height: auto;
}

a, a:visited, a:active {
  text-decoration: none;
  color: inherit;
}

/*Other CSS global rules*/
html {
  scroll-behavior: smooth;
}

body {
  background-color: #FFFFFF;
  color: #000000;
}

@font-face {
  font-family: "Montserrat";
  src: url("/assets/fonts/Montserrat-Light.woff2") format("woff2"), url("/assets/fonts/Montserrat-Light.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("/assets/fonts/Montserrat-Bold.woff2") format("woff2"), url("/assets/fonts/Montserrat-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("/assets/fonts/Montserrat-Medium.woff2") format("woff2"), url("/assets/fonts/Montserrat-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Courier Prime";
  src: url("/assets/fonts/CourierPrime-Regular.woff2") format("woff2"), url("/assets/fonts/CourierPrime-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
html {
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: 50%;
  line-height: 1;
}
@media only screen and (min-width: 30em) {
  html {
    font-size: 60%;
  }
}
@media only screen and (min-width: 48em) {
  html {
    font-size: 62.5%;
  }
}
@media only screen and (min-width: 75em) {
  html {
    font-size: 75%;
  }
}

.heading {
  margin-block-start: 0em;
  margin-block-end: 0em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  font-family: "Montserrat", sans-serif;
}
.heading--1 {
  font-size: 4rem;
  color: #481A72;
  text-transform: uppercase;
}
.heading--2 {
  font-size: 2.5rem;
}
.heading--3 {
  font-size: 2rem;
}

.paragraph {
  font-size: 1.6rem;
  line-height: 1.2;
}

.clr-white {
  color: #FFFFFF;
}

.clr-purple {
  color: #481A72;
}

.clr-grey {
  color: #4C4452;
}

/*Default Gutenberg editor styles*/
.container--page h1, .container--page h2, .container--page h3, .container--page h4, .container--page h5, .container--page h6, .container--page p {
  margin-block-start: 0em;
  margin-block-end: 0em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  font-family: "Montserrat", sans-serif;
}
.container--page h1 {
  font-size: 4rem;
  color: #481A72;
  text-transform: uppercase;
  padding-bottom: 2rem;
}
.container--page h2 {
  font-size: 2.5rem;
  padding-bottom: 2rem;
}
.container--page h3 {
  font-size: 2.5rem;
  color: #481A72;
  padding-bottom: 2rem;
}
.container--page h4 {
  font-size: 2rem;
  padding-bottom: 2rem;
}
.container--page p {
  font-size: 1.6rem;
  line-height: 1.2;
  padding-bottom: 1rem;
}

.container {
  width: 100%;
}
.container__inner {
  max-width: 120rem;
  margin: auto;
  padding-left: 2rem;
  padding-right: 2rem;
}
.container--scroll-reveal {
  transition: transform 1s, opacity 1s;
}
.container--scroll-hide {
  opacity: 0;
  transform: translateY(8rem);
}
.container--clr {
  background-color: #481A72;
  background-image: linear-gradient(135deg, #481A72, #725786);
  color: #FFFFFF;
}
.container--clr img {
  max-width: 25rem;
}
.container--bg-grey {
  background-color: #F5F5F5;
}

.d-flex-space-btwn {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.d-flex-2 {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
.d-flex-2--rev {
  order: -1;
}
@media only screen and (min-width: 48em) {
  .d-flex-2--rev {
    order: 2;
  }
}

.flex-2-child-50 {
  flex-basis: 100%;
  max-width: 60rem;
  padding: 1rem;
}
@media only screen and (min-width: 48em) {
  .flex-2-child-50 {
    flex-basis: 50%;
  }
}

.content-mx-60 {
  max-width: 60rem;
  margin: auto !important;
}

.content-mx-35 {
  max-width: 35rem;
  margin: auto;
}

.d-grid-2 {
  display: grid;
  grid-template-columns: 1fr;
  row-gap: 3rem;
  column-gap: 4rem;
  justify-content: center;
  align-items: flex-start;
}
@media only screen and (min-width: 48em) {
  .d-grid-2 {
    grid-template-columns: 1fr 1fr;
  }
}
@media only screen and (min-width: 64em) {
  .d-grid-2 {
    grid-template-columns: 1fr 1fr 1fr;
  }
}
.d-grid-2 * {
  justify-self: center;
}

.d-grid-auto {
  display: grid;
  grid-template-columns: 1fr;
  row-gap: 2rem;
  column-gap: 4rem;
}
@media only screen and (min-width: 48em) {
  .d-grid-auto {
    grid-template-columns: repeat(auto-fit, minmax(1rem, max-content));
  }
}

.single_sidebar {
  display: grid;
  grid-template-columns: 1fr;
  row-gap: 2rem;
  column-gap: 6rem;
}
@media only screen and (min-width: 64em) {
  .single_sidebar {
    grid-template-columns: 2fr 1fr;
  }
}
.single_sidebar aside {
  background-color: #F5F5F5;
  padding: 2rem;
  border-radius: 1rem;
}
.single_sidebar aside img {
  max-width: 20rem;
  aspect-ratio: 1/1;
  object-fit: contain;
  border: 1px solid #481A72;
  background-color: #481A72;
  box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);
}
.single_sidebar aside h4 {
  margin-bottom: 4rem !important;
}
.single_sidebar aside .sidebar__items {
  display: block;
}
@media only screen and (min-width: 30em) {
  .single_sidebar aside .sidebar__items {
    display: grid;
    grid-template-columns: 1fr 1fr;
    row-gap: 2rem;
    column-gap: 2rem;
  }
}
@media only screen and (min-width: 48em) {
  .single_sidebar aside .sidebar__items {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }
}
@media only screen and (min-width: 64em) {
  .single_sidebar aside .sidebar__items {
    display: block;
  }
}

/*Paddings*/
.pt-sm {
  padding-top: 2rem;
}

.pb-sm {
  padding-bottom: 2rem;
}

.pt-md {
  padding-top: 4rem;
}

.pb-md {
  padding-bottom: 4rem;
}

.pt-lr {
  padding-top: 6rem;
}

.pb-lr {
  padding-bottom: 6rem;
}

/*Other*/
.txt-center {
  text-align: center;
}

.border-around-img-2px {
  border: 2px solid #000000;
}

.shadow {
  box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);
}

.header {
  padding: 0.5rem 0;
  background-color: #FFFFFF;
  position: sticky;
  top: 0;
  z-index: 20;
}
.header__left {
  display: flex;
  align-items: center;
}
.header__left ul li {
  color: #481A72;
  font-size: 1.6rem;
  font-weight: 600;
  cursor: pointer;
  margin: 0rem 2rem;
  list-style: none;
}
.header__logo {
  max-width: 10rem;
}
.header__menu {
  display: none;
}
@media only screen and (min-width: 48em) {
  .header__menu {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    color: #4C4452;
  }
}
.header__burger {
  width: 3rem;
  height: 2rem;
  display: flex;
  align-items: center;
  cursor: pointer;
  z-index: 20;
}
@media only screen and (min-width: 48em) {
  .header__burger {
    display: none;
  }
}
.header__burger__middle {
  position: relative;
  transition: all 0.2s;
}
.header__burger__middle, .header__burger__middle::before, .header__burger__middle::after {
  width: 3rem;
  height: 3px;
  background-color: #4C4452;
}
.header__burger__middle::before, .header__burger__middle::after {
  content: "";
  position: absolute;
  transition: all 0.5s;
}
.header__burger__middle::before {
  top: -8px;
}
.header__burger__middle::after {
  top: 8px;
}
.header__burger__middle--active {
  background-color: transparent !important;
}
.header__burger__middle--active::before {
  top: 0 !important;
  transform: rotate(45deg);
  transition: all 0.5s;
}
.header__burger__middle--active::after {
  top: 0 !important;
  transform: rotate(-45deg);
  transition: all 0.5s;
}

.navigation-top ul {
  list-style: none;
  display: block;
}
@media only screen and (min-width: 48em) {
  .navigation-top ul {
    display: flex;
  }
}
.navigation-top ul li {
  position: relative;
  font-size: 1.6rem;
  font-weight: 600;
  margin-right: 1rem;
  color: #FFFFFF;
  padding: 0.5rem 0.7rem 0.5rem 3rem;
  background-image: linear-gradient(120deg, transparent 0rem, transparent 2rem, #481A72 2rem);
}
.navigation-top ul li:hover {
  background-image: linear-gradient(120deg, transparent 0rem, transparent 2rem, #000000 2rem);
}
.navigation-top ul .li-shop-desktop {
  background-image: linear-gradient(120deg, transparent 0rem, transparent 2rem, #000000 2rem);
}
.navigation-top ul .li-shop-desktop:hover {
  background-image: linear-gradient(120deg, transparent 0rem, transparent 2rem, #4C4452 2rem);
}

.navigation-top ul li:hover .sub-menu {
  display: block;
}

.navigation-top ul li .sub-menu {
  display: none;
  position: absolute;
  left: -10%;
  width: 25rem;
  padding-top: 1rem;
  border-bottom: 1px solid white;
}
.navigation-top ul li .sub-menu li {
  width: 100%;
  background-image: none;
  background-color: #481A72;
}
.navigation-top ul li .sub-menu li:hover {
  background-color: #000000;
}

.icon-linkedin__svg {
  width: 2rem;
  height: 2rem;
  fill: #481A72;
}
@media only screen and (min-width: 30em) {
  .icon-linkedin__svg {
    width: 1.8rem;
    height: 1.8rem;
  }
}
.icon-linkedin__svg:hover {
  fill: #000000;
}

.mobile-menu {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 10;
  background-color: #481A72;
  display: flex;
  justify-content: center;
  padding-top: 10rem;
  transition: all 0.3s;
  visibility: visible;
}
@media only screen and (min-width: 48em) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu--close {
  left: -200%;
  transition: all 0.7s;
  visibility: hidden;
}
.mobile-menu ul {
  list-style: none;
}
.mobile-menu ul li {
  font-size: 3rem;
  font-weight: 600;
  margin-bottom: 2rem;
  color: #FFFFFF;
}

.mobile-menu__nav ul li .sub-menu {
  margin-top: 2rem;
  padding-left: 1.5rem;
}
.mobile-menu__nav ul li .sub-menu li {
  font-size: 2.5rem;
}

.header__burger__middle--open,
.header__burger__middle--open::before,
.header__burger__middle--open::after {
  background-color: #FFFFFF;
}

.mobile-menu-open-logo {
  position: absolute;
  top: 1rem;
  left: 1rem;
  z-index: 20;
  transition: all 0.3s;
  visibility: visible;
}
@media only screen and (min-width: 48em) {
  .mobile-menu-open-logo {
    display: none;
  }
}
.mobile-menu-open-logo img {
  max-width: 10rem;
}
.mobile-menu-open-logo--hide {
  left: -200%;
  transition: all 0.7s;
  visibility: hidden;
}

.footer {
  background-color: #4C4452;
  color: #FFFFFF;
}
.footer__nav {
  font-weight: 400;
}
.footer__nav ul {
  list-style: none;
  display: block;
}
@media only screen and (min-width: 30em) {
  .footer__nav ul {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(24rem, 1fr));
    grid-gap: 1.5rem;
  }
}
.footer__nav ul li {
  font-size: 2rem;
  margin-bottom: 3rem;
}
@media only screen and (min-width: 48em) {
  .footer__nav ul li {
    font-size: 1.6rem;
  }
}
.footer__nav ul li a:hover {
  color: #A0A0A0;
}
.footer__text {
  font-family: "Courier Prime", monospace;
  text-align: justify;
  max-width: 30rem;
  font-size: 1.4rem;
}
.footer__text a {
  text-decoration: underline;
  font-weight: 600;
}

.footer__nav ul li .sub-menu {
  display: block;
  margin-top: 1rem;
}
.footer__nav ul li .sub-menu li {
  margin-bottom: 1rem;
}

.footer__icons {
  display: flex;
}
.footer__icons .footer-svg {
  max-width: 4rem;
  margin-right: 2rem;
}
.footer__icons .footer-icon__linkedin {
  width: 3rem;
  height: 3rem;
  fill: #FFFFFF;
}
.footer__icons .footer-icon__github {
  width: 3.2rem;
  height: 3.2rem;
  fill: #FFFFFF;
}

/*Home page template styles*/
.hero {
  background-color: #481A72;
  background-image: linear-gradient(135deg, #481A72, #725786);
}
.hero__content {
  text-align: center;
  color: #FFFFFF;
}
@media only screen and (min-width: 48em) {
  .hero__content {
    text-align: left;
  }
}
.hero__content--paragraph {
  padding: 2rem 0;
}
.hero__content--paragraph p {
  font-family: "Courier Prime", monospace;
  font-size: 2rem;
}

.intro {
  background-color: #FFFFFF;
}
.intro img {
  max-width: 30rem;
}

.home-page-el--hide {
  opacity: 0;
  transform: translateY(2rem);
}
.home-page-el--reveal {
  transition: transform 1s, opacity 1s;
}

@keyframes fadeInAnimation {
  0% {
    opacity: 0;
    transform: translateY(4rem);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
.hero figure {
  animation: fadeInAnimation ease 1s;
}

.hero .hero__content {
  animation: fadeInAnimation ease 1s;
}

.intro {
  animation: fadeInAnimation ease 1s;
}

.image-bckgrd {
  background-color: #481A72;
  background-image: radial-gradient(circle, #481a72, #532a77, #5e397c, #684881, #725786);
  aspect-ratio: 1/1;
  width: 100%;
  max-width: 30rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 100%;
  margin: auto;
}

.about {
  background-color: #481A72;
  color: #FFFFFF;
}

/*Basic button styles for the website*/
.btn {
  text-decoration: none;
  outline: none;
  text-transform: uppercase;
  min-width: 12rem;
  font-size: 1.6rem;
  font-weight: 700;
  padding: 1rem 3rem;
  text-align: center;
  border-radius: 5rem;
  border: none;
  cursor: pointer;
  display: inline-block;
}
.btn:hover {
  transform: translateY(-2px);
}
.btn--white {
  background-color: #FFFFFF;
  color: #000000 !important;
}
.btn--purple {
  background-color: #481A72;
  color: #FFFFFF !important;
}

.call-to-contact {
  background-color: #481A72;
  text-align: center;
}

/*Home page 3 boxes about services. Might be reused in other places later*/
.boxes {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.box {
  flex-basis: 100%;
  max-width: 30rem;
  margin: 1rem;
  padding: 4rem 1rem;
  text-align: center;
  box-shadow: 0 1.5rem 1.5rem 0 rgba(0, 0, 0, 0.05), 0 0 1.5rem 0 rgba(0, 0, 0, 0.05);
  border-radius: 0.7rem;
}
.box:hover {
  transform: translateY(-2px);
  box-shadow: 0 1.6rem 1.6rem 0 rgba(0, 0, 0, 0.1), 0 0 1.6rem 0 rgba(0, 0, 0, 0.1);
}
.box__image {
  max-width: 15rem;
  margin: auto;
  padding-bottom: 2rem;
}
.box__image img {
  aspect-ratio: 1/1;
  object-fit: contain;
}
.box__content h4 {
  font-size: 1.6rem;
  text-transform: uppercase;
  color: #000000;
  padding-bottom: 2rem;
}
.box__content p {
  font-size: 1.4rem;
  line-height: 1.2;
  color: #4C4452;
  padding: 0rem 2rem;
}

/*Contact form styles*/
.contact-form {
  max-width: 80rem;
  margin: auto;
  margin-top: 2rem;
  margin-bottom: 5rem;
  background-color: #481A72;
  background-image: linear-gradient(135deg, #481A72, #725786);
  color: #FFFFFF;
  border-radius: 5px;
  padding: 3rem;
  box-shadow: 0 2.4rem 4.8rem rgba(0, 0, 0, 0.15);
  display: grid;
  grid-template-columns: 1fr;
  row-gap: 2rem;
  align-items: center;
}
@media only screen and (min-width: 48em) {
  .contact-form {
    grid-template-columns: 3fr 2fr;
    column-gap: 2rem;
  }
}
.contact-form__img {
  max-width: 30rem;
  justify-self: center;
}
.contact-form__text {
  grid-row: 2;
  justify-self: center;
  width: 100%;
  max-width: 50rem;
  margin: auto;
}
@media only screen and (min-width: 48em) {
  .contact-form__text {
    grid-row: 1;
    justify-self: left;
  }
}
.contact-form label {
  display: block;
  font-size: 1.8rem;
  margin-bottom: 1rem;
}
.contact-form input, .contact-form textarea {
  width: 100%;
  padding: 1rem;
  font-size: 1.6rem;
  font-family: inherit;
  color: #000000;
  border: none;
  background-color: #FFFFFF;
  border-radius: 9px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
}
.contact-form textarea {
  resize: vertical;
}
.contact-form .error-border {
  border: 3px solid #D54464;
}

.contact-form *:focus {
  outline: none;
  box-shadow: 0 0 0 0.8rem rgba(253, 242, 233, 0.5);
}

.error-name {
  color: #D54464;
}

.display-none {
  display: none !important;
}

#website {
  display: none;
}

/*EXAMPLES custom post type archive and single page styles */
.examples-category-banner {
  background-color: #481A72;
  background-image: linear-gradient(135deg, #481A72, #725786);
  color: #FFFFFF;
}
.examples-category-banner__inner {
  display: grid;
  row-gap: 2rem;
  align-items: center;
  column-gap: 1rem;
  justify-content: center;
  grid-template-columns: 1fr 1fr;
}
@media only screen and (min-width: 48em) {
  .examples-category-banner__inner {
    grid-template-columns: 15rem 1fr 15rem;
    column-gap: 2rem;
  }
}
.examples-category-banner__icon {
  max-width: 15rem;
  grid-row: 2;
  justify-self: center;
  box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);
}
@media only screen and (min-width: 48em) {
  .examples-category-banner__icon {
    grid-row: 1;
  }
}
.examples-category-banner__text {
  grid-column: 1/span 2;
}
@media only screen and (min-width: 48em) {
  .examples-category-banner__text {
    grid-row: 1;
    grid-column: 2;
  }
}

.example-box {
  width: 100%;
  max-width: 35rem;
}
@media only screen and (min-width: 48em) {
  .example-box {
    max-width: 40rem;
  }
}
.example-box__img {
  width: 100%;
  padding-top: 100%;
  position: relative;
  border: 2px solid #481A72;
  box-sizing: border-box;
}
.example-box__img img {
  max-width: 25rem;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
@media only screen and (min-width: 48em) {
  .example-box__img img {
    max-width: 30rem;
  }
}
.example-box__img:hover .example-box__overlay {
  opacity: 0;
}
.example-box__overlay {
  background: rgba(72, 26, 114, 0.85);
  position: absolute;
  height: 100.5%;
  width: 101%;
  left: -1px;
  top: -1px;
  bottom: 0;
  right: 0;
  opacity: 1;
  z-index: 10;
  transition: all 0.4s ease-in-out 0s;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  text-align: center;
}
@media only screen and (min-width: 30em) {
  .example-box__overlay {
    width: 100.5%;
  }
}
.example-box__overlay p {
  color: #FFFFFF !important;
  font-weight: 600;
}

.container--page .example-box .example-box__paragraph {
  font-family: "Courier Prime", monospace;
  font-size: 1.8rem;
  color: #4C4452;
  line-height: 1;
}

.single-example__banner {
  background-color: #481A72;
  background-image: linear-gradient(135deg, #481A72, #725786);
  font-family: "Courier Prime", monospace;
}
.single-example__banner img {
  max-width: 15rem;
  box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);
}

